            // 1. If the usage category is clicked, run slickUnfilter on the slider.

            // 2. Then loop through all of the usage models and remove the class slide-show, the slide-show class should already be 'and always be' on the 'persistent-slides / non-usage model category slides'

            // 3. Then add the class of slide-shown to the usage-model-categories that have a data attribute that equals that of the active usage model category thumbnail.

            // 4. then run the slick filter and filter all slides with the class slide-shown. The persistent slides should always have this class so they will always be visible, the usage-model-cateogry slides will be filtered if they don't have the class.

            // 5. if you want to remove all of the usage-model-category slides, if the length is < 0, loop through the usage model slides and remove the class slide shown.

